body {
  color: #ae7c78;
  background-color: #ffffff;
  font-family: "Montserrat", "Arial", "Helvetica", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  min-width: 1220px;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  font: inherit;
  color: inherit;
  text-decoration: none;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
}

.content-container {
  margin: 0 auto;
  padding: 0 10px;
  width: 1200px;
}

.content-container-small {
  margin: 0 auto;
  width: 794px;
}

.header {
  position: relative;
  margin-top: 25px;
  padding: 22px 0 21px;
  border-top: 1px solid #ae7c78;
  border-bottom: 1px solid #ae7c78;
}

.logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button {
  display: inline-block;
  vertical-align: middle;
  padding: 30px 70px;
  font: inherit;
  color: #ffffff;
  background-color: #d89999;
  appearance: none;
  border: none;
  border-radius: 40px;
}
.button:focus {
  outline: none;
}
.button:hover, .button:focus {
  background-color: #ca7474;
}
.button:active {
  background-color: #c36262;
}

.nav {
  display: block;
}
.nav__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.nav__item:nth-child(6n+3) {
  margin-right: 97px;
}
.nav__link:focus {
  outline: none;
}
.nav__link:hover, .nav__link:focus {
  text-decoration: underline;
}
.nav__link:visited {
  color: #ae7c78;
}
.nav__link:active {
  color: #583735;
}

.promo {
  padding-top: 80px;
