}
.promo .content-container {
  display: flex;
}
.promo__block {
  margin-right: 24px;
  max-width: 384px;
}
.promo__heading {
  margin-top: 0;
  margin-bottom: 24px;
  font-family: "Playfair Display", "Times New Roman", "Georgia", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 44px;
  line-height: 59px;
